body {
  width: 100%;
  height: 100%;
}

body {
  background: linear-gradient(
    -45deg,
    #ee7752,
    #e73c7e,
    #23a6d5,
    #23d5ab,
    #ee7757,
    #a73c7e
  );
  background-size: 400% 400%;
  animation: gradient 12s ease infinite;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.game_name {
  font-family: cursive;
  text-decoration: none;
  font-size: 30px;
  margin: 0;
  color: rgba(225, 225, 225, 0.01);
  background-image: url("https://images.unsplash.com/photo-1499195333224-3ce974eecb47?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=2cf549433129d4227d1879347b9e78ce&auto=format&fit=crop&w=1248&q=80");
  background-repeat: repeat;
  -webkit-background-clip: text;
  animation: animate 25s ease-in-out infinite;
  text-align: center;
  text-transform: uppercase;
  font-weight: 900;
}

@keyframes animate {
  0%,
  100% {
    background-position: left top;
  }
  25% {
    background-position: right bottom;
  }
  50% {
    background-position: left bottom;
  }
  75% {
    background-position: right top;
  }
}

.score {
  color: rgb(249, 249, 252);
}

svg path {
  cursor: pointer;
}

.continent-header {
  background-color: #c7bbcf;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;
  top: 0;
}

.continent-header ul {
  list-style-type: none;

  display: flex;
  justify-content: right;
}

.continent-header li {
  display: inline;
  // object-fit: contain;
  // mix-blend-mode: color-burn;
  color: white;
  padding: 3px;
}

.continent-header li.bchoose {
  object-fit: contain;
  object-fit: contain;
  mix-blend-mode: color-burn;
  border-radius: 0;
}

.continent-header button:hover {
  background-color: #ccc;
}

#my_div {
  padding: 15px;
  margin-left: 25px;
  width: 60%;
  position: relative;
  overflow: scroll;
  border: 2px outset rgb(45, 18, 57);
  border-radius: 25px;
  border-block-color: rgb(60, 5, 5);
  box-shadow: 5px 10px #888888;
}

h2 {
  font-family: Brush Script;
  font-style: italic;
  color: turquoise;
}

#title {
  display: flex;
  justify-content: center;
  color: aquamarine;
  font-size: 30px;
  width: fit-content;
  margin: auto;
  margin-top: 25px;
}

#play {
  display: flex;
  flex-direction: column;
  width: 25%;
  flex-wrap: wrap;
  justify-content: space-around;
  justify-items: center;
  position: relative;
  overflow: scroll;
  border: 2px outset rgb(45, 18, 57);
  border-radius: 25px;
  box-shadow: 5px 10px #888888;
  height: 500px;
  font-size: 40px;

  img {
    padding: 5px;
    width: 100px;
    height: 80px;
    border-radius: 10px;
    text-shadow: #888888;
    align-items: center;
    width: auto;
  }

  p {
    font-size: 25px;
    color: rgb(3, 51, 53);
    font-family: fantasy;
  }
}

#container {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
  height: 100%;
}

#allSvg {
  transform-origin: 90% -20%; /* Sets the origin of the transformation to the top-left corner of the SVG */
  transform: scale(
    1.168
  ); /* Adjust the scale factor to zoom in or out (e.g., 2 for 200% zoom) */
}

.aus_class {
  transform-origin: 105% 105%; /* Sets the origin of the transformation to the top-left corner of the SVG */
  transform: scale(
    3.3
  ); /* Adjust the scale factor to zoom in or out (e.g., 2 for 200% zoom) */
}

.af_class {
  transform-origin: 48% 73%; /* Sets the origin of the transformation to the top-left corner of the SVG */
  transform: scale(2);
}

.as_class {
  transform-origin: 100% 15%; /* Sets the origin of the transformation to the top-left corner of the SVG */
  transform: scale(1.7);
}

.nam_class {
  transform-origin: -10% 8%; /* Sets the origin of the transformation to the top-left corner of the SVG */
  transform: scale(2.1);
}
.sam_class {
  transform-origin: 3% 110%; /* Sets the origin of the transformation to the top-left corner of the SVG */
  transform: scale(2.2);
}
.eu_class {
  transform-origin: 75% -20%; /* Sets the origin of the transformation to the top-left corner of the SVG */
  transform: scale(1.9);
}

path:hover {
  fill: pink;
  // transform: scale(1.5);
}

img {
  position: relative;
  height: 200px;
  width: 200px;
}

.bchoose {
  display: flex;
  border-radius: 20px;
  overflow: hidden;
  text-decoration: none;
  color: rgb(59, 57, 57);
  padding: 8px 12px;
  border: none;
  outline: none;
  transition: background-color 0.3s ease;
  font-family: fantasy;
  font-style: italic;
  text-decoration: underline;

  button {
    margin: 10px;
    height: 25px;
    border-radius: 20px;
    box-shadow: #3d07cf;
    object-fit: contain;
    object-fit: contain;
    mix-blend-mode: color-burn;
  }

  img {
    width: auto;
    border-radius: 20px;
  }
}

.main_buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  position: relative;
  @media (max-width: 400px) {
    flex-direction: column;
  }
}

.main_buttons div {
  width: 23%;
  margin-bottom: 30px;
  flex-basis: 23%;
  box-sizing: border-box;
}

#score_container {
  border: 3px solid white;
  margin: auto;
  display: flex;
  font-size: 25px;
  font-family: Verdana,Geneva,Tahoma,sans-serif;
  color: #fff;
  justify-content: center;
  align-items: center;
  width: 120px;
  background: #1f8da385;;;
  border-radius: 10px;

  // div {
  //   display: flex;
  //   justify-content: right;

  //   .score {
  //     margin-right: 10px;
  //     align-items: center;
  //   }
  // }
}

.choose img {
  mix-blend-mode: color-burn;
}

#play_music,
#pause_music,
#github,
#linkedin {
  height: 25px;
  width: 25px;
  background-color: none;
  border: none;
  padding-right: 15px;
}

#music_button,
#github_button,
#linkedin_button {
  object-fit: contain;
  mix-blend-mode: color-burn;
  border: none;
  padding: 5px;
}

.wave-animation {
  display: inline-block;
  animation: wave 1.5s linear infinite;
  font-family: cursive;
}

@keyframes wave {
  0% {
    transform: translateY(0);
  }
  20% {
    transform: translateY(-10px);
  }
  50% {
    transform: translateY(-20px);
  }
  70% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

.info {
  padding: 5px;
  color: white;
}

